.btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  line-height: 1.4;
  background-color: $orange;
  color: #fff;
  text-align: center;
  padding: 15px 30px;
  cursor: pointer;
  transition: background-color .3s;
  font-weight: 700;
  width: 400px;
  max-width: 100%;
  border: 1px solid $orange;
  z-index: 1;
  overflow: hidden;
  text-transform: uppercase;
  @media (max-width: $tablet) {
    font-size: 18px;
    line-height: 1.4;
  }
  @media (max-width: $mobile) {
    font-size: 16px;
    line-height: 1.4;
  }
  @media (max-width: $mobileSmall) {
    font-size: 14px;
    line-height: 1.4;
  }
  @media (max-width: $mobileSmall) {
    min-width: unset;
    width: 100%;
  }

  &--mini {
    padding: 12px 30px;
    height: 50px;
    font-size: 14px;
  }

  &--br{
    border-radius:10px;
  }

  &--orange {
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: -100%; //     
      width: 100%;
      height: 100%;
      background-color: #fff;
      z-index: -1; //   
      transition: left 0.2s ease;
    }

    &:hover {
      color: $orange;

      &::before {
        left: 0; //    
      }
    }
  }

  &--transparent {
    background-color: transparent;
    color: #fff;
    border-color: #fff;
    transition: border-color .3s, color .3s;

    &:hover {
      color: $orange;
      border-color: $orange;
    }
  }

  &--transparent-hov-black {
    background-color: transparent;
    color: $orange;

    &:hover {
      color: #fff;
      border-color: #000;
      background-color: #000;
    }
  }

  &--black{
    background-color:#000;
    color:#fff;
    border-color:#000000;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      width: 0;
      height: 100%;
      background-color: #fff;
      border-color:#000000;
      z-index: -1;
      transition: all 0.2s ease;
      left:50%;
      right:50%;
    }
    &::after{
      content: '';
      position: absolute;
      top: 0;
      width: 0;
      height: 100%;
      background-color: #fff;
      border-color:#000000;
      z-index: -1;
      transition: all 0.2s ease;
      left:50%;
      right:50%;
    }
    &:hover {
      color:#000;

      &::before {
        left: 0;
        width: 50%;
      }
      &::after{
        right: 0;
        width: 50%;
      }
    }
  }

  &--black-hov-transparent {
    background-color: #000;
    color: #fff;
    border-color: #000;

    &:hover {
      color: #000;
      border-color: #000;
      background-color: transparent;
    }
  }


  &--transparent-color-black {
    background-color: transparent;
    color: black;
    border-color: black;

    &:hover {
      color: $orange;
      border-color: $orange;
    }
  }

  &--border {
    border: 1px solid #C56940;
    background-color: transparent;
    color: #C56940;
  }

  &--white {
    background-color: #fff;
    color: #000000;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: -100%; //     
      width: 100%;
      height: 100%;
      background-color: #000;
      z-index: -1; //   
      transition: left 0.2s ease;
    }

    &:hover {
      color: #fff;

      &::before {
        left: 0; //    
      }
    }
  }

  &--blue {
    border: 1px solid #274D74;
    background-color: #274D74;
    color: #fff;

    &.btn--border {
      background-color: #fff;
      color: #274D74;
    }
  }


}

.nav-next,
.nav-prev {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  width: 35px;
  height: 35px;
  cursor: pointer;
  color: #fff;
  background-color: $orange;
  border-radius: 0;

  @media (max-width: $mobile){
    width: 28px;
    height: 28px;
    font-size: 13px;
  }
  @media (max-width: $mobileSmall){
    width: 22px;
    height: 22px;
    font-size: 12px;
  }

  &--black {
    color: $orange;
    background-color: #1a1a1a;
  }

  &.swiper-button-lock {
    display: none;
  }

  &.swiper-button-disabled {
    opacity: .5;
    cursor: auto;
  }
}


