@import "fonts";
@import "null";
@import "mixins";
@import "./vendors/swiper";


$orange: #ff7e00;
$grey: #e6e6e6;
$laptop: 1240px;
$tablet: 991.92px;
$mobile: 767.92px;
$mobileSmall: 575.98px;
@import "utils";
@import "forms";
@import "typegr";
@import "btns";
@import "modal";
@import "./layout/header";
@import "./layout/footer";

html {
  scroll-behavior: smooth;
}

body.blocked {
  overflow: hidden;
}


body.overlayed {
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #000;
    opacity: .2;
    z-index: 100;
    cursor: pointer;
  }
}

body.lock {
  overflow: hidden;
}

.container {
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;
  width: 100%;
  padding: 0 48px;
  @media (max-width: $tablet) {
    padding: 0 32px;
  }
  @media (max-width: $mobile) {
    padding: 0 24px;
  }

  &--narrow {
    max-width: 1200px;
    @media (max-width: 1300px) {
      padding: 0 48px;
      max-width: 100%;
    }
    @media (max-width: $tablet) {
      padding: 0 32px;
    }
    @media (max-width: $mobile) {
      padding: 0 24px;
    }
  }
}

body {
  font-family: "Montseratt", sans-serif;
  color: #000;
  font-size: 24px;
  line-height: 1.5;
  height: 100%;
  display: flex;
  flex-direction: column;
  font-weight: 400;
  @media (max-width: $laptop) {
    font-size: 20px;
    line-height: 1.4;
  }
  @media (max-width: $tablet) {
    font-size: 18px;
    line-height: 1.4;
  }
  @media (max-width: $mobile) {
    font-size: 16px;
    line-height: 1.4;
  }
}

main {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
}

.padding-for-header {
  padding-top: 160px;
  @media (max-width: $tablet) {
    padding-top: 125px;
  }
  @media (max-width: $mobileSmall) {
    padding-top: 110px;
  }
}

section {
  padding: 48px 0;
  @media (max-width: $tablet) {
    padding: 36px 0;
  }
}


.title-block {
  background-color: #000000;
  padding: 0;
  color: #fff;
  overflow: hidden;

  &__bg {
    position: absolute;
    top: 50%;
    right: 0;
    height: 100%;
    width: 50%;
    object-position: left;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: left;
    }
  }

  &__wrapper {
    padding: 48px 0;
  }

  &__title {
    text-transform: uppercase;
    width: 66%;
  }
}

.video-block {
  position: relative;
  padding: 0;
  @media (max-width: $mobileSmall) {
    display: flex;
    flex-direction: column;
  }

  &__info {
    position: absolute;
    right: 0;
    bottom: 0;
    background-color: #fff;
    padding: 50px;
    width: 640px;
    @media (max-width: $laptop) {
      padding: 30px;
      width: 46%;
      min-width: 420px;
    }
    @media (max-width: $mobileSmall) {
      position: static;
      width: 100%;
      left: 0;
      min-width: unset;
    }
  }

  &__text {
    margin-bottom: 20px;
  }

  &__btn {
    .btn {
      width: 100%;
    }
  }

  &__media {
    video {
      display: block;
      height: 100%;
      max-height: 700px;
      width: 100%;
      object-fit: cover;
    }
  }
}

.introduce-block {
  background-color: #000;
  color: #fff;

  &__arrow-icon {
    display: inline-block;
    position: absolute;
    top: 0;
    right: 0;
    transform: rotate(-45deg);
    font-size: 120px;
    @media (max-width: $laptop) {
      font-size: 100px;
    }
    @media (max-width: $tablet) {
      font-size: 80px;
    }
    @media (max-width: $mobile) {
      font-size: 60px;
    }
  }

  &--orange {
    background-color: $orange;
    color: #000;
  }

  &__wrapper {
    position: relative;
    @media (max-width: $tablet) {
      padding-top: 24px;
      padding-bottom: 12px;
    }
  }

  &__title-block {
    margin-bottom: 30px;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__info {
    width: 50%;
    margin-left: auto;
    @media (max-width: $mobile) {
      width: 100%;
    }
  }

  &__btn {
    @media (max-width: $mobile) {
      width: 100%;
    }
  }

  &__desc {
    font-size: 28px;
    line-height: 1.4;
    margin-bottom: 1em;
    font-weight: 500;
    @media (max-width: $laptop) {
      font-size: 24px;
      line-height: 1.4;
    }
    @media (max-width: $tablet) {
      font-size: 20px;
      line-height: 1.4;
    }
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 1.4;
      p {
        display: inline;
      }
    }
  }

  &__title {
    text-transform: uppercase;
  }

  &__letter-logo {
    display: inline-block;
    margin-top: 20px;

    img {
      width: 100%;
    }
  }
}

.two-parts {
  background-color: #000;
  color: #fff;
  padding-bottom: 0;

  &__title {
    text-transform: uppercase;
    margin-bottom: 1em;
  }

  &__top {
    position: relative;
    height: 205px;
    background-repeat: repeat-x;
    @media (max-width: $laptop) {
      height: 12vw;
      background-size: 100%;
    }
  }

  &__wrapper {
    position: relative;

    &::after {
      content: '';
      position: absolute;
      width: calc(50vw - 50%);
      background-color: $grey;
      top: 0;
      right: calc(50% - 50vw);
      bottom: 0;
      @media (max-width: $mobile) {
        content: none;
      }
    }
  }

  &__content {
    display: flex;
    @media (max-width: $mobile) {
      flex-wrap: wrap;
    }
  }

  &__part {
    display: flex;
    flex-direction: column;
    width: 50%;
    flex-shrink: 0;
    padding: 32px;
    padding-left: 0;
    @media (max-width: $mobile) {
      width: 100%;
      padding-left: 0;
      padding-right: 0;
    }

    & + .two-parts__part {
      background-color: $grey;
      padding-right: 0;
      padding-left: 32px;
      color: #000;
      text-align: right;
      @media (max-width: $mobile) {
        position: relative;
        width: 100vw;
        margin-left: -24px;
        padding: 24px;
      }

      .two-parts__buttons {
        .btn {
          margin-left: auto;
        }
      }
    }
  }

  &__buttons {
    display: flex;
    margin-top: auto;

    .btn + .btn {
      margin-left: 20px;
    }
  }
}

.coach-preview {
  background-color: #fff;

  &__name {
    text-transform: uppercase;
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
  }

  &__info {
    width: 30%;
    flex-shrink: 0;
    padding-right: 24px;
    @media (max-width: $tablet) {
      width: 35%;
    }
    @media (max-width: $mobile) {
      width: 50%;
    }
  }

  &__achieve {
    font-size: 36px;
    line-height: 1.4;
    @media (max-width: $laptop) {
      font-size: 32px;
      line-height: 1.4;
    }
    @media (max-width: $tablet) {
      font-size: 24px;
      line-height: 1.4;
    }
    @media (max-width: $mobile) {
      font-size: 20px;
      line-height: 1.4;
    }
    @media (max-width: $mobileSmall) {
      font-size: 16px;
      line-height: 1.4;
    }
  }

  &__card {
    display: flex;
    justify-content: space-between;
    width: 70%;
    padding-left: 24px;
    @media (max-width: $tablet) {
      width: 65%;
    }
    @media (max-width: $mobile) {
      width: 50%;
      flex-direction: column;
    }
  }

  &__btn {
    margin-top: -100px;
    width: 30%;
    @media (max-width: $laptop) {
      margin-top: -40px;
    }
    @media (max-width: $tablet) {
      margin-top: 0;
      width: 45%;
    }
    @media (max-width: $mobile) {
      width: 100%;
      margin-top: 20px;
      .btn {
        width: 100%;
      }
    }
  }

  &__photo {
    width: 50%;
    margin-left: 20px;
    @media (max-width: $mobile) {
      width: 100%;
      margin-top: 20px;
      margin-left: 0;
    }

    img {
      width: 100%;
    }
  }

  &__post {
    font-size: 24px;
    line-height: 1.4;
    color: $orange;
    font-weight: 600;
    margin-top: 40px;
    @media (max-width: $laptop) {
      font-size: 20px;
      line-height: 1.4;
      margin-top: 30px;
    }
    @media (max-width: $tablet) {
      font-size: 18px;
      line-height: 1.4;
      margin-top: 20px;
    }
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 1.4;
    }
  }
}

[class*='__prev-text'] {
  font-weight: 500;
  margin-right: 80px;
  @media (max-width: $laptop) {
    margin-right: 40px;
  }
  @media (max-width: $tablet) {
    margin-right: 20px;
  }
  @media (max-width: $mobile) {
    margin-bottom: 30px;
    margin-right: 0;
  }
}

.slider-cards-block {

  &__slider {
    position: relative;


    [class*='__next'] {
      display: none;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 10px;
      @media (max-width: $mobile) {
        display: inline-flex;
      }
    }

    [class*='__prev'] {
      display: none;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 10px;
      @media (max-width: $mobile) {
        display: inline-flex;
      }
    }
  }

  [class*='__photo'] {
    width: 100%;
    height: 16vw;
    @media (max-width: $laptop) {
      height: 20vw;
    }
    @media (max-width: $mobile) {
      height: 40vw;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  @media (min-width: 768px) {
    &__slider {
      .swiper-wrapper {
        display: flex;
        flex-wrap: wrap;
        margin: -24px;
        @media (max-width: $laptop) {
          margin: -18px;
        }

        .swiper-slide {
          width: calc(25% - 48px);
          margin: 24px;
          @media (max-width: $laptop) {
            width: calc((100% / 3) - 36px);
            margin: 18px;
          }
        }
      }

      &--second {
        .swiper-wrapper {
          .swiper-slide {
            width: calc(25% - 48px);
            margin: 24px;
            @media (max-width: $laptop) {
              width: calc((100% / 3) - 36px);
              margin: 18px;
            }
            @media (max-width: $tablet) {
              width: calc((100% / 2) - 36px);
            }
          }
        }
      }
    }
  }

  .swiper-slide {
    &--empty {
      @media (max-width: $tablet) {
        display: none;
      }
    }
  }
}

.programs {
  background-color: #000;
  color: #fff;

  &__title {
    text-transform: uppercase;
    margin-bottom: .8em;
  }

  &__subtitle {
    margin-bottom: 1em;
  }

  &__letter {
    display: flex;
    justify-content: center;
    margin: 0 auto;
    width: 100px;

    img {
      width: 100%;
    }
  }
}

.programs-card {
  background-color: #fff;
  color: #000;
  transition: background-color .3s, color .3s;
  height: auto;

  &:hover {
    &:not(.programs-card--catalog) {
      background-color: $orange;
      color: #fff;

      .programs-card__photo {
        &.triangle-right-bottom {
          &::before {
            border-bottom-color: $orange;
          }
        }
      }
    }
  }

  &__title {
    font-weight: 900;
    text-transform: uppercase;
    margin-bottom: 15px;
  }

  &__price {
    font-size: 16px;
    line-height: 1.4;
    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 1.4;
    }
  }

  &--prev,
  &--last {
    background-color: #000000;
    color: #fff;
    @media (max-width: $mobile) {
      display: none;
    }

    &:hover {
      background-color: #000000;
      color: #fff;
    }
  }

  &--last {
    img {
      max-width: 100%;
    }
  }

  &__info {
    padding: 24px;
    @media (max-width: $mobile) {
      padding: 12px;
    }
  }

  &__photo {
    img {
      width: 100%;
    }

    &.triangle-right-bottom {
      &::before {
        transition: border-bottom-color .3s;
        border-bottom-color: #fff;
      }
    }
  }
}

.about-photo {
  background-color: #1a1a1a;
  color: #fff;

  &__wrapper {
    display: flex;
    @media (max-width: $mobile) {
      flex-direction: column;
    }
  }

  &__title {
    text-transform: uppercase;
  }

  &__prev-text {
    font-weight: 500;
    margin-right: 80px;
    @media (max-width: $laptop) {
      margin-right: 40px;
    }
    @media (max-width: $tablet) {
      margin-right: 20px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 30px;
      margin-right: 0;
    }
  }

  &__img {
    width: 33%;
    flex-shrink: 0;
    @media (max-width: $laptop) {
      width: 25%;
    }
    @media (max-width: $mobile) {
      width: 100%;
    }
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }

    &.triangle-right-bottom {
      &::before {
        border-bottom-color: #1a1a1a;
      }
    }

    img {
      width: 100%;
    }
  }

  &__info {
    margin-left: 60px;
    @media (max-width: $laptop) {
      margin-left: 40px;
    }
    @media (max-width: $tablet) {
      margin-left: 20px;
    }
    @media (max-width: $mobile) {
      margin-left: 0;
    }
  }

  &__btn {
    margin-top: 60px;
    @media (max-width: $tablet) {
      margin-top: 40px;
    }
    @media (max-width: $mobile) {
      margin-top: 20px;
    }
  }
}

.sportsmen {
  background-color: #000000;
  color: #fff;
  padding-bottom: 0;

  &__title {
    text-transform: uppercase;
    margin-bottom: 16px;
  }

  &__subtitle {
    width: 50%;
    margin-left: auto;
    margin-bottom: 40px;
    @media (max-width: $tablet) {
      width: 100%;
    }
  }

  .programs-card {
    background-color: #000000;
    color: #fff;

    .programs-card__photo {
      &.triangle-right-bottom {
        &::before {
          border-bottom-color: #000000;
        }
      }
    }

    &:hover {
      background-color: $orange;
      color: #fff;

      .programs-card__photo {
        &.triangle-right-bottom {
          &::before {
            border-bottom-color: $orange;
          }
        }
      }
    }
  }

  &__letters {
    position: relative;
    height: 205px;
    background-repeat: repeat-x;
    margin-top: 40px;
    @media (max-width: $laptop) {
      height: 12vw;
      background-size: 100%;
    }
  }
}

.simple-form {
  background-color: #191919;
  color: #fff;

  &.triangle-right-bottom {
    &::before {
      border-bottom-color: #e6e6e6;
    }
  }

  &__content {
    max-width: 1100px;
    flex-grow: 1;
  }

  &__wrapper {
    display: flex;
    padding: 30px 0;
    @media (max-width: $mobile) {
      flex-direction: column;
    }
    @media (max-width: $mobile) {
      padding: 15px 0;
    }
    @media (max-width: $mobileSmall) {
      padding: 5px 0 10px;
    }
  }

  &__title {
    text-transform: uppercase;
    margin-bottom: 60px;
    @media (max-width: $tablet) {
      margin-bottom: 40px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }
  }

  .btn {
    white-space: nowrap;
  }

  .form-line {
    @media (max-width: $tablet) {
      max-width: 80%;
    }
    @media (max-width: $mobile) {
      width: 100%;
      max-width: unset;
    }
  }

  &--second {
    background-color: #000;

    .simple-form__content {
      max-width: unset;
      display: flex;
      flex-wrap: wrap;
    }

    .simple-form__subtitle {
      width: 420px;
      margin: 0 auto;
      @media (max-width: $laptop) {
        width: 100%;
        margin-bottom: 20px;
      }
    }

    .form-line {
      width: 60%;
      margin-left: auto;
      @media (max-width: $laptop) {
        width: 100%;
        margin-left: -10px;
      }
      @media (max-width: $tablet) {
        width: 500px;
        .btn,
        .form-input {
          width: calc(100% - 20px);
        }
      }
      @media (max-width: $mobileSmall) {
        width: calc(100% + 20px);
      }
    }
  }
}

.form-line {
  display: flex;
  margin: -10px;

  @media (max-width: $tablet) {
    flex-direction: column;
  }


  .form-input,
  .btn {
    margin: 10px;
    @media (max-width: $mobile) {
      width: 100%;
    }
  }

  .form-input {
    flex-grow: 5;
  }

  .btn {
    flex-grow: 1;
  }
}

.trainers {
  background-color: #e6e6e6;

  &.triangle-right-bottom {
    &::before {
      border-bottom-color: $orange;
      z-index: 2;
      border-left: 156px solid transparent;
      border-bottom: 132px solid $orange;
      right: 0;
      bottom: 0;
      @media (max-width: $mobile) {
        border-left: 80px solid transparent;
        border-bottom: 70px solid $orange;
      }
    }
  }

  &__title {
    text-transform: uppercase;
    margin-bottom: 48px;
    @media (max-width: $tablet) {
      margin-bottom: 30px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }
  }

  &__footer {
    position: relative;
    margin-top: 40px;

    img {
      width: 100%;
      object-fit: contain;
    }

    @media (max-width: $laptop) {
      height: 12vw;
      background-size: 100%;
    }
  }

  &__btn {
    margin-top: 20px;
  }
}

.trainer-card {
  background-color: #fff;
  color: #000;
  transition: background-color .3s, color .3s;
  height: auto;

  &:hover {
    background-color: $orange;
    color: #fff;

    .trainer-card__photo {
      &.triangle-right-bottom {
        &::before {
          border-bottom-color: $orange;
        }
      }
    }
  }

  &__title {
    font-weight: 900;
    text-transform: uppercase;
    margin-bottom: 15px;
    font-size: 36px;
    line-height: 1;
    white-space: pre-wrap;
    word-spacing: 100vw;
    @media (max-width: $tablet) {
      font-size: 24px;
      line-height: 1;
    }
    @media (max-width: $mobile) {
      font-size: 24px;
      line-height: 1;
    }
  }

  &__price {
    font-size: 16px;
    line-height: 1.4;
    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 1.4;
    }
  }

  &--prev,
  &--last {
    background-color: transparent;
    @media (max-width: $mobile) {
      display: none;
    }

    &:hover {
      background-color: #e6e6e6;
      color: #000;
    }
  }

  &--last {
    img {
      max-width: 100%;
    }
  }

  &__info {
    padding: 24px;
    @media (max-width: $mobile) {
      padding: 12px;
    }
  }

  .trainer-card__photo {
    height: 24vw;
    @media (max-width: $mobile) {
      height: 70vw;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover
    }

    &.triangle-right-bottom {
      &::before {
        transition: border-bottom-color .3s;
        border-bottom-color: #fff;
      }
    }
  }
}

.locations {
  background-color: $orange;
  padding-bottom: 0;

  &__map {
    margin-top: 40px;
  }

  &__btn {
    margin-top: 20px;
  }

  &__title {
    position: absolute;
    width: calc(50% - 93px);
    text-transform: uppercase;
    white-space: pre-wrap;
    word-spacing: 100vw;
    @media (max-width: $tablet) {
      position: relative;
      margin: 0 20px 20px;
      width: calc(100% - 40px);
    }
  }

  .slider-cards-block__slider [class*="__prev"] {
    left: 0px;
  }

  .slider-cards-block__slider [class*="__next"] {
    right: 0px;
  }

  &__map {
    iframe {
      width: 100%;
      display: block;
    }
  }
}


.location-card {
  height: auto;

  &__wrapper {
    display: inline-block;
    width: 100%;
    background-color: #fff;
    color: #000;
    height: 100%;
    transition: background-color .3s, color .3s;
    padding-bottom: 40px;
