.client-card {
  position: relative;
  overflow: hidden;
  @media (max-width: 1079.98px) {
    background-color: $orange;
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $orange;
    opacity: 0;
    transition: opacity .3s;
  }

  &:hover {
    @media (min-width: 1080px) {
      &::before {
        opacity: 1;
      }

      .client-card__info {
        opacity: 1;
        transform: translateX(0);
      }

      .client-card__name {
        transform: translateX(0);
      }

      .client-card__text,
      .client-card__btns {
        transform: translateX(0);
      }
    }
  }

  &__info {
    padding: 40px;
    transition: opacity .3s;
    overflow-y: auto;
    color: #fff;
    @media (min-width: 1080px) {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      opacity: 0;
      color: #fff;
    }
    @media (max-width: 1600px) {
      padding: 20px;
    }
    @media (max-width: 1079.98px) {
      padding: 15px 10px;
    }
  }

  &__text,
  &__btns {
    @media (min-width: 1080px) {
      transform: translateX(-100px);
      transition: transform .6s;
    }
  }

  &__text {
    font-size: 16px;
    line-height: 1.4;
    @media (max-width: 1600px) {
      font-size: 14px;
      line-height: 1.2;
    }
  }

  &__btns {
    margin-top: 20px;
  }

  &__name {
    font-size: 24px;
    line-height: 1;
    font-weight: 900;
    margin-bottom: 30px;
    @media (min-width: 1080px) {
      transition: transform .3s;
      transform: translateX(-100px);
    }
    @media (max-width: $tablet) {
      font-size: 20px;
      line-height: 1;
      margin-bottom: 20px;
    }
    @media (max-width: $mobile) {
      font-size: 18px;
      line-height: 1;
      margin-bottom: 16px;
    }
  }

  .client-card__photo {
    height: 32vw;
    max-height: 620px;
    @media (max-width: $mobile) {
      height: 65vw;
    }
  }
}

