.trainer-page {
  background-color: #fff;
  color: #000;
  padding-bottom: 140px;
  @media (max-width: $laptop) {
    padding-top: 120px;
  }
  @media (max-width: $tablet) {
    padding-bottom: 80px;
  }
  @media (max-width: $mobile) {
    padding-bottom: 60px;
  }

  &__bg-left {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    max-width: 50%;
    z-index: 0;
    opacity: .3;

    img {
      width: 100%;
      object-fit: contain;
    }
  }


  &__bg-right {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    max-width: 50%;
    z-index: 0;

    img {
      width: 100%;
      object-fit: contain;
    }
  }

  &__bg-right,
  &__bg-left {
    & ~ .container {
      position: relative;
      z-index: 2;
    }
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(3, calc((100% / 3) - 32px));
    grid-template-rows: repeat(5, auto);
    grid-column-gap: 48px;
    @media (max-width: $tablet) {
      grid-template-columns: repeat(2, calc((100% / 2) - 48px));
    }
    @media (max-width: $mobile) {
      grid-template-columns: 1fr;
      grid-column-gap: 0;
    }
  }

  &__photo-block {
    grid-column: 1;
    grid-row: 1/6;
    @media (max-width: $tablet) {
      grid-row: 1/3;
    }
    @media (max-width: $mobile) {
      grid-row: 1;
      grid-column: 1;
    }

    img {
      width: 100%;
    }
  }

  &__title {
    grid-column: 2/4;
    grid-row: 1;
    margin-bottom: 40px;
    @media (max-width: $mobile) {
      grid-row: 2;
      grid-column: 1;
      margin-top: 20px;
    }
  }

  &__first-col {
    grid-row: 2/6;
    grid-column: 2;
    @media (max-width: $tablet) {
      grid-row: 2;
      grid-column: 2;
    }
    @media (max-width: $mobile) {
      grid-row: 3;
      grid-column: 1;
    }
  }

  &__second-col {
    grid-row: 2/6;
    grid-column: 3;
    @media (max-width: $tablet) {
      grid-row: 4/6;
      grid-column: 1/3;
      margin-top: 20px;
    }
  }

  &__quote {
    display: flex;
    align-items: center;
    background-color: $orange;
    color: #fff;
    padding: 20px;
    font-size: 18px;
    line-height: 1.4;
    font-weight: 500;

    span {
      font-size: 48px;
      line-height: 1;
      font-weight: 900;
      margin-right: 16px;
    }

  }

  &__name {
    margin-bottom: 16px;
  }

  &__post {
    text-transform: uppercase;
    color: $orange;
    font-weight: 900;
  }

  &__subtitle {
    font-weight: 900;
    margin-bottom: 16px;
    text-transform: uppercase;
    font-size: 20px;
    line-height: 1;
  }

  &__info-block {
    font-size: 16px;
    line-height: 1.4;
    font-weight: 500;

    & + .trainer-page__info-block {
      margin-top: 40px;
    }
  }

  &__education-block + .trainer-page__education-block {
    margin-top: 20px;
  }
}

