    width: 100%;
    margin-bottom: 16px;
    @media (max-width: 1440px) {
      height: 39vw;
    }
    @media (max-width: $mobile) {
      height: 60vw;
    }
    @media (max-width: $mobileSmall) {
      height: 90vw;
    }

    &:hover {
      .product-item__first-half,
      .product-item__second-half {
        opacity: .5;
      }
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &--two {
      .second {
        display: none;
      }
    }
  }

  &__first-half,
  &__second-half {
    position: absolute;
    width: calc(50% - 1px);
    height: 100%;
    bottom: 0;

    &::before {
      content: '';
      position: absolute;
      bottom: 0;
      background-color: $orange;
      height: 3px;
      width: 100%;
      left: 1px;
      border-radius: 30px;
    }
  }

  &__first-half {
    left: 0;
    opacity: 1;

    &:hover {
      opacity: 1 !important;

      & ~ .first {
        display: block;
      }

      & ~ .second {
        display: none;
      }
    }
  }

  &__second-half {
    right: 0;
    opacity: .5;

    &:hover {
      opacity: 1 !important;

      & ~ .first {
        display: none;
      }

      & ~ .second {
        display: block;
      }
    }
  }


  &__title {
    display: inline-block;
    font-size: 22px;
    line-height: 1.2;
    font-weight: 700;
    text-transform: uppercase;
    @include hover-effect;
    margin-bottom: 16px;
