h1, .h1 {
  font-size: 92px;
  font-weight: 900;
  line-height: 1;
  @media (max-width: $laptop) {
    font-size: 64px;
    line-height: 1;
  }
  @media (max-width: $tablet) {
    font-size: 48px;
    line-height: 1;
  }
  @media (max-width: $mobile) {
    font-size: 30px;
    line-height: 1;
  }
}

h2, .h2 {
  font-size: 72px;
  font-weight: 900;
  line-height: 1;
  @media (max-width: $laptop) {
    font-size: 64px;
    line-height: 1;
  }
  @media (max-width: $tablet) {
    font-size: 48px;
    line-height: 1;
  }
  @media (max-width: $mobile) {
    font-size: 36px;
    line-height: 1;
  }
  @media (max-width: $mobileSmall) {
    font-size: 24px;
    line-height: 1;
  }
}
.h2-little{
  font-size:48px;
  line-height: 1;
  font-weight: 900;
  @media (max-width: $laptop){
    font-size:40px;
    line-height: 1;
  }
  @media (max-width: $tablet){
    font-size:32px;
    line-height: 1;
  }
  @media (max-width: $mobile){
    font-size:24px;
    line-height: 1;
  }
}

h3, .h3 {
  font-size:40px;
  line-height: 1;
  font-weight: 900;
  @media (max-width: $laptop){
    font-size:32px;
    line-height: 1;
  }
  @media (max-width: $tablet){
    font-size:24px;
    line-height: 1;
  }
  @media (max-width: $mobile){
    font-size:20px;
    line-height: 1;
  }
}

ul, ol, li {
  list-style-type: disc;
}

ul, ol {
  padding-left: 16px;
  margin-bottom: .7em;

  li + li {
    margin-top: 8px;
  }
}