.header {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 99;
  height: 160px;
  @media (max-width: $tablet) {
    height: 125px;
  }
  @media (max-width: $mobileSmall) {
    height: 110px;
  }

  ul{
    margin-left: 0;
    padding-left: 0;
  }

  .black-logo {
    display: none;
    @media (max-width: $tablet) {
      display: block;
    }
  }

  &--scrolled-up {
    position: fixed;
    left: 0;
    right: 0;
    opacity: 1;
    box-shadow: 0px 4px 24px 0px #ADADADA6;

    .white-logo {
      display: none;
    }

    .black-logo {
      display: block;
    }

    .header-content {
      background-color: #fff;
      color: #000;
    }
  }

  .white-logo {
    @media (max-width: $tablet) {
      display: none;
    }
  }

  &--scrolled-down {
    position: fixed;
    left: 0;
    right: 0;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
  }

  &[data-open] {
    @media (max-width: $tablet) {
      position: fixed;
      z-index: 10;
      left: 0;
      right: 0;
    }

    .header-content__burger-trigger {
      span {
        transform: rotate(45deg) translateX(-5px) translateY(-10px);

        &::after {
          content: none;
        }

        &::before {
          transform: rotate(90deg) translateX(8px);
        }
      }
    }
  }
}

.header-top {
  font-size: 14px;
  padding: 10px;
  background-color: #000000;
  color: #FFFFFF;
  height: 55px;
  overflow: hidden;
  @media (max-width: $mobileSmall) {
    height: 40px;
    padding: 5px 0;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
  }

  &__contacts {
    display: flex;
    align-items: center;
    font-weight: 500;

    span {
      margin-left: 8px;
    }
  }

  &__contact + .header-top__contact {
    margin-left: 30px;
    @media (max-width: $tablet) {
      margin-left: 15px;
    }
  }

  &__contact {
    display: flex;
    align-items: center;
    white-space: nowrap;
    @media (max-width: $mobileSmall) {
      i {
        font-size: 12px;
      }
    }

    span {
      margin-left: 8px;
    }
  }

  &__socials {
    display: flex;
    align-items: center;
    margin: 0 -5px;
  }

  &__soc-icon {
    width: 35px;
    height: 35px;
    font-size: 16px;
    margin: 0 5px;
    overflow: hidden;
    cursor: pointer;
    transition: background-color .3s;
    border-radius: 6px;
    @media (max-width: $mobileSmall) {
      width: 30px;
      height: 30px;
    }

    i {
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 35px;
      transition: transform .3s;
      @media (max-width: $mobileSmall) {
        height: 30px;
      }
    }

    &:hover {
      background-color: $orange;
      color: #fff;

      i {
        transform: translateY(-35px);
        @media (max-width: $mobileSmall) {
          transform: translateY(-30px);
        }
      }
    }
  }
}

.header-content {
  //background-color: #000000;
  color: #FFFFFF;
  @media (max-width: $tablet) {
    background-color: #FFFFFF;
    color: #000000;
  }

  &[data-open] {

  }

  &__wrapper {
    display: flex;
    align-items: center;
    padding: 30px 0;
    @media (max-width: $tablet) {
      position: relative;
      padding: 15px 0;
    }
  }

  &__nav {
    position: relative;
    font-size: 12px;
    @media (max-width: 1200px) {
      font-size: 10px;
    }
    @media (max-width: $tablet) {
      font-size: 12px;
      display: none;
      overflow-y: auto;
      overflow-x: hidden;
      padding-bottom: 20px;
      background-color: #fff;
      right: 0;
      position: fixed;
      top: 125px;
      padding-left: 32px;
      padding-right: 32px;
      width: 100%;
      max-height: calc(100% - 125px);
    }
    @media (max-width: $mobileSmall) {
      top: 110px;
    }

    a {
      @media (max-width: $tablet) {
        width: 100%;
      }
    }
  }

  &__logo {
    width: 52px;
    flex-shrink: 0;
    margin-right: auto;
    @include hover-effect;
    @media (max-width: $tablet) {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
    }

    img, svg {
      width: 100%;
    }
  }

  &__list {
    display: flex;
    text-transform: uppercase;
    font-weight: 500;
    margin: 0 -4px;
    list-style-type: none;
    padding-left: 0;
    li{
      list-style-type: none;
    }
    @media (max-width: $tablet) {
      flex-direction: column;
    }

    & > li {
      height: auto;
      margin: 0 4px;
      @media (max-width: $tablet) {
        & + li {
          border-top: 1px solid #ebeef6;
        }
      }
    }

    .header-content__sub-icon {
      height: auto;
    }

    li {
      cursor: pointer;

      & > a:hover {
        background-color: $orange;
        color: #fff;
        @media (max-width: $tablet) {
          background-color: #fff;
          color: #000000;
        }
      }

      &[data-active] > .header-content__sub-box,
      .header-content__sub-box:hover {
        background-color: $orange;
        color: #fff;
        @media (max-width: $tablet) {
          background-color: #fff;
          color: #000000;
        }

        .header-content__sub-icon {
          background-color: $orange;
          color: #fff;
          @media (max-width: $tablet) {
            background-color: #fff;
            color: #000000;
          }
        }
      }

      &.active {
        & > a {
          color: $orange;


          &:hover {
            color: #fff;
            @media (max-width: $tablet) {
              color: $orange;
            }
          }
        }

        &:hover {
          color: #fff;
          @media (max-width: $tablet) {
            background-color: #fff;
            color: #000000;
          }

        }
      }

      & > a {
        display: inline-block;
        padding: 8px 20px;
        @media (max-width: 1200px) {
          padding: 5px 10px;
        }
        @media (max-width: $tablet) {
          padding: 12px 0;
        }
      }
    }
  }

  &__sublist {
    position: absolute;
    border: 1px solid #ededed;
    border-radius: 5px;
    background-color: #fff;
    z-index: -1;
    opacity: 0;
    top: calc(100% + 10px);
    visibility: hidden;
    color: #000000;
    @media (max-width: $tablet) {
      position: static;
      border: none;
      width: 100%;
      padding-left: 20px;
      display: none;
    }

    li,
    a {
      width: 100%;
    }

    li {
      position: relative;

      &:first-child {
        a {
          border-top-left-radius: 5px;
          border-top-right-radius: 5px;
        }
      }

      &:last-child {
        a {
          border-bottom-left-radius: 5px;
          border-bottom-right-radius: 5px;
        }
      }
    }

    .header-content__sublist {
      position: absolute;
      left: 100%;
      width: 300px;
      top: 0;
      @media (max-width: $tablet) {
        position: static;
        width: 100%;
        padding-left: 20px;
      }
    }

    &[data-active] {
      visibility: visible;
      z-index: 1;
      opacity: 1;
      @media (max-width: $tablet) {
        display: block;
      }
    }
  }

  &__sub-box {
    display: flex;
    align-items: center;
    height: 100%;
    cursor: pointer;
    @media (max-width: $tablet) {
      position: relative;
    }

    a {
      padding-top: 8px;
      padding-bottom: 8px;
      padding-left: 20px;
      @media (max-width: 1200px) {
        padding-top: 5px;
        padding-bottom: 5px;
        padding-left: 10px;
      }
      @media (max-width: $tablet) {
        padding: 12px 0;
      }
    }

    .header-content__sub-icon {
      padding-right: 20px;
      @media (max-width: 1200px) {
        padding-right: 10px;
      }
    }
  }

  &__sub-icon {
    height: auto;
    padding-right: 5px;
    padding-left: 5px;

    & > i {
      transition: transform .3s;
    }

    @media (max-width: $tablet) {
      width: 100px;
      text-align: right;
      padding-top: 12px;
      padding-bottom: 12px;
    }
  }

  .header-content__with-submenu[data-active] > .header-content__sub-box {
    & > .header-content__sub-icon {
      & > i {
        transform: rotate(180deg);
      }
    }
  }

  &__burger-trigger {
    display: none;
    @media (max-width: $tablet) {
      display: flex;
      order: -1;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      width: 35px;
      height: 40px;
      cursor: pointer;
      -webkit-transition: opacity 0.3s;
      transition: opacity 0.3s;
      span {
        display: inline-block;
        height: 3px;
        width: 24px;
        background: #000000;
        content: "";
        right: 0;
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
        -webkit-transform-origin: 0% 50%;
        -ms-transform-origin: 0% 50%;
        transform-origin: 0% 50%;
        -webkit-transition: -webkit-transform 0.2s;
        transition: -webkit-transform 0.2s;
        transition: transform 0.2s;
        transition: transform 0.2s, -webkit-transform 0.2s;

        &::before,
        &::after {
          position: absolute;
          display: inline-block;
          height: 3px;
          width: 24px;
          background: #000000;
          content: "";
          right: 0;
          -webkit-transition: -webkit-transform 0.2s;
          transition: -webkit-transform 0.2s;
          transition: transform 0.2s;
          transition: transform 0.2s, -webkit-transform 0.2s;
        }

        &::before {
          top: -8px;
        }

        &::after {
          top: 8px;
        }
      }
    }
  }

  &__with-submenu {
    @media (max-width: $tablet) {
      position: relative;
    }
  }
}
