.footer {
  background-color: #000000;
  color: #fff;

  &__wrapper {
    display: flex;
    padding: 64px 0;
    @media (max-width: $tablet) {
      flex-direction: column;
      padding: 48px 0;
    }
    @media (max-width: $mobileSmall) {
      padding: 24px 0;
    }
  }

  &__logo {
    display: inline-block;
    margin-bottom: 20px;
  }

  &__history {
    font-size: 16px;
    line-height: 1.4;
    font-weight: 500;

    p {
      margin-bottom: 1em;
    }
  }

  &__subtitle {
    font-size: 20px;
    line-height: 1.4;
    font-weight: 900;
    text-transform: uppercase;
    margin-bottom: 30px;
    @media (max-width: $tablet) {
      margin-bottom: 20px;
    }
    @media (max-width: $mobileSmall) {
      margin-bottom: 16px;
    }
  }

  &__links {
    display: flex;
    flex-direction: column;

    a {
      display: inline-block;
      padding: 5px 0;
      font-size: 16px;
      line-height: 1;
      font-weight: 500;
      @include hover-effect;

      & + a {
        margin-top: 5px;
      }
    }
  }

  &__socials {
    margin-top: 20px;

    a {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 45px;
      height: 45px;
      background-color: $orange;
      border-radius: 5px;
      transition: background-color .3s, color .3s;

      &:hover {
        background-color: #fff;
        color: $orange;
      }

      & + a {
        margin-left: 8px;
      }
    }
  }

  &__main {
    width: 50%;
    padding: 0 24px;
    @media (max-width: $tablet) {
      width: 100%;
      padding: 0;
    }
  }

  &__contacts,
  &__info {
    width: 25%;
    padding: 0 24px;
    @media (max-width: $tablet) {
      width: 100%;
      padding: 24px 0;
    }
  }

  &__nav {
    font-size: 16px;
    line-height: 1.4;
    font-weight: 500;

    ul {
      list-style-type: none;
      padding-left: 0;
    }

    li {
      list-style-type: none;
      & + li {
        margin-top: 10px;
      }
    }

    a {
      color: $orange;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &__copyright {
    border-top: 1px solid #333333;
  }

  &__copyright-wrapper {
    padding: 24px 0;

    .container {
      display: flex;
      justify-content: space-between;
      font-size: 16px;
      line-height: 1.4;
      @media (max-width: $mobileSmall) {
        flex-direction: column;
        div + div {
          margin-top: 10px;
        }
      }
    }
  }

  &__picture {
    img {
      width: 100%;
    }
  }
}