.corner-right-bottom {
  position: absolute;
  z-index: 0;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;

  &::before {
    content: '';
    display: inline;
    position: absolute;
    z-index: 0;
    width: 0;
    height: 0;
    border-left: 100vw solid transparent;
    border-bottom: 7vw solid #000;
    right: 0;
    bottom: 0;
  }

  &--white {
    &::before {
      border-bottom: 7vw solid #fff;
    }
  }

  &--lblack {
    &::before {
      border-bottom: 7vw solid #1a1a1a;
    }
  }

  &--orange {
    &::before {
      border-bottom: 7vw solid $orange;
    }
  }
}

.transparent-right-bottom {
  margin-bottom: -7vw;

  & + section {
    padding-top: calc(7vw - 3px);
    margin-top: 1px;
  }

  &::before {
    content: '';
    display: inline;
    position: absolute;
    z-index: 1;
    width: 0;
    height: 0;
    border-left: 100vw solid black;
    border-bottom: 7vw solid transparent;
    right: 0;
    bottom: 0;
  }

  &--white {
    &::before {
      border-bottom: 7vw solid transparent;
    }
  }

  &--lblack {
    &::before {
      border-left: 100vw solid #1a1a1a;
    }
  }
}

section {
  position: relative;

  &.transparent-right-bottom {
    position: relative;
    margin-bottom: -7vw;
    padding-bottom: 80px;
    @media (max-width: $mobileSmall) {
      padding-bottom: 40px;
    }

    .container {
      position: relative;
      z-index: 1;
    }
  }
}

.b-lblack {
  background-color: #1a1a1a !important;

  &.transparent-right-bottom {
    &::before {
      border-left: 100vw solid #1a1a1a !important;
    }
  }
}

.b-black {
  background-color: #000 !important;

  &.transparent-right-bottom {
    &::before {
      border-left: 100vw solid black !important;
    }
  }
}


[data-animation="fade-up"] {
  opacity: 0;

  &[data-activated] {
    animation: fadeInUp .5s ease-out forwards;
  }
}

[data-animation="fade-up-2"] {
  opacity: 0;

  &[data-activated] {
    animation: fadeInUp2 .7s ease-out forwards;
  }
}

[data-animation="fade-up-3"] {
  opacity: 0;

  &[data-activated] {
    animation: fadeInUp2 .5s ease-out forwards;
    animation-delay: .4s;
  }
}


[data-animation="fade-up-4"] {
  opacity: 0;

  &[data-activated] {
    animation: fadeInUp2 .5s ease-out forwards;
    animation-delay: .7s;
  }
}
[data-animation="fade-up-5"] {
  opacity: 0;

  &[data-activated] {
    animation: fadeInUp2 .5s ease-out forwards;
    animation-delay: 1s;
  }
}

[data-animation="fade-up-6"] {
  opacity: 0;

  &[data-activated] {
    animation: fadeInUp2 .5s ease-out forwards;
    animation-delay: 1.2s;
  }
}


[data-animation="fade-left"] {
  opacity: 0;

  &[data-activated] {
    animation: fadeInLeft .5s ease-out forwards;
  }
}

[data-animation="custom-fade-1"] {
  opacity: 0;

  &[data-activated] {
    animation: customFade1 .5s ease-out forwards;
  }
}

@keyframes customFade1 {
  0% {
    opacity: 0;
    transform: translateY(-50%) translateX(calc(30% + 100px));
  }
  100% {
    opacity: 1;
    transform: translateY(-50%) translateX(30%);
  }
}

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(100px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(100px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp2 {
  0% {
    opacity: 0;
    transform: translateY(70px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

